import Vue from 'vue'
import Router from 'vue-router'
import layout from '../views/layout/layout.vue'
import iframe from '../components/iframe/iframe.vue'
const _import = require('./_import.js')
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: layout,
      redirect: '/index',
      children: [
        {
          path: '/index',
          name: 'default',
          component: _import('dashloash/dashloash')
        },
        {
          path: '/menuList',
          name: 'menuList',
          component: _import('menuList/menuList')
        },
        {
          path: '/systemManage',
          name: 'systemManage',
          component: _import('systemManage/systemManage')
        },
        {
          path: '/userManage',
          name: 'userManage',
          component: _import('userManage/userManage')
        },
        {
          path: '/iframe',
          name: 'iframe',
          component: iframe
        }

      ]
    },
    {
      path: '/login',
      name: 'login',
      component: _import('login/login')
    }
  ]
})

export const constantRouterMap = []

export const aysncRouterMap = []



