<template>
    <div>
        <div class="toolbar">
            <el-button>新增</el-button>
            <el-button type="success">复制新增</el-button>
            <el-button type="primary">修改</el-button>
            <el-button type="danger">删除</el-button>
            <el-button type="info">导出数据</el-button>
            <el-button type="info">菜单排序</el-button>
            <el-button type="warning">上传文件</el-button>
            <el-button type="danger">关联数据</el-button>
        </div>
        <el-table
            :data="tableData"
            class="tabTitle"
            border>
            <el-table-column
                type="expand"
                width="80">
            </el-table-column>
            <el-table-column
                label="菜单名称"
                width="180">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.date }}</span>
                </template>
            </el-table-column>
            <el-table-column
                label="菜单编号"
                width="180">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>姓名: {{ scope.row.name }}</p>
                        <p>住址: {{ scope.row.address }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.name }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column label="打开方式">
                <template slot-scope="scope">
                </template>
            </el-table-column>
            <el-table-column label="是否启用">
                <template slot-scope="scope">
                </template>
            </el-table-column>
            <el-table-column label="iconClass">
                <template slot-scope="scope">
                </template>
            </el-table-column>
            <el-table-column label="url">
                <template slot-scope="scope">
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                tableData: [{
                    date: '2016-05-02',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-04',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1517 弄'
                }, {
                    date: '2016-05-01',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1519 弄'
                }, {
                    date: '2016-05-03',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1516 弄'
                }]
            }
        },
        methods: {
            handleEdit(index, row) {
                console.log(index, row);
            },
            handleDelete(index, row) {
                console.log(index, row);
            }
        }
    }
</script>
<style>
.tabTitle{
    width: 100%;
    margin-top: 6px;
}
</style>



