<template>
    <div class="tagview">
        <ul class="tab-contanier">
            <li v-for="(item,key) in visitedViews" :key="key" :class="[item.id==='default'?'default-active':'',activeTagView===item.id?'click-active':'']" @click="clickTab(item)" @contextmenu.prevent="contextmenu(item,$event)">
                <i v-if="item.id==='default'" class="el-icon-date"></i>
                <span>{{item.text}}</span>
            </li>
        </ul>
        <div class="contextmenu" :style="style" v-show="isshow" @mouseleave="out">
            <ul>
                <li @click="operTag('current')">关闭当前</li>
                <li @click="operTag('all')">关闭全部</li>
                <li @click="operTag('others')">关闭其他</li>
            </ul>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data() {
      return {
          style: {
              top: 0,
              left: 0
          },
          isshow: false,
          current: ''
      }
  },
  methods: {
      clickTab(tabObj) {
          this.$store.dispatch('changeActiveTag', tabObj.id)
          this.$emit('changeTab', tabObj.id)
      },
      contextmenu(val,e) {
          console.log(val.id)
          this.current = val
          if (val.id === 'default') return false
          this.style.top = e.pageY - 5 + 'px'
          this.style.left = e.pageX - 5 + 'px'
          this.isshow = true
      },
      out() {
          this.isshow = false
      },
      operTag(val) {
          this.isshow = false
          this.currentId = ''
          if (val === 'all') {  // 关闭全部
              this.$store.dispatch('delAll')
              this.$router.push({
                  name: 'default'
              })
          }
          if(val === 'others'){ // 关闭除当前的
            console.log(this.current)
            this.$store.dispatch('delOthers', this.current)

          }
          if(val === 'current'){ // 关闭当前的
            this.$store.dispatch('delCurrent', this.current)
            this.$router.push({
                name: this.activeTagView
            })
          }

      }
  },
  computed: {
      ...mapGetters([
          'visitedViews',
          'activeTagView',
          'showPosition'
      ])
  },
  created: function () {
  }/* ,
  mounted: function () {
      this.$store.dispatch('updatevisitedViews', this.currentTagView)
      .then(res => {
          this.tagsList = res
      })
  } */
}
</script>


<style>
.tagview {
    height: 38px;
    line-height: 38px;
    width: 100%;
    color: #7f96ac;
}
.tagview{
    padding: 0 10px;
    border-bottom: 2px solid #0093dd;
}
.tab-contanier li {
    padding: 0 15px;
    display: inline-block;
    border-right: 1px solid #ddd;
    cursor: pointer;
    font-size: 15px;
}
.tab-contanier li.click-active {
    background-color: #0093dd;
    color: #fff;
}
.contextmenu {
    position: fixed;
    z-index: 99;
    background-color: #fff;
    cursor: pointer;
}
.contextmenu ul li {
    font-size: 14px;
    padding: 0 15px;
    height: 30px;
    line-height: 30px;
}
.contextmenu ul li:hover {
    background-color: #F2F7FE;
    color: #333;
}
</style>



