const app = {
    state: {
        iframeurl: ''
    },
    mutations: {
        SET_IFRAMEURL: (state, iframeurl) => {
            state.iframeurl = iframeurl
        }
    },
    actions: {
        setIframeUrl ({ commit }, iframeurl) {
            return new Promise(resolve => {
                commit('SET_IFRAMEURL', iframeurl)
                resolve()
            })
        }
    }
}

export default app


