<template>
    <header>
        <div class="top-left-logo">
            <section class="logo-icon"></section>
            <h1 class="logo-text">金政基础平台jzee测试</h1>
        </div>
        <div class="top-right-action">
            <ul>
                <li class="jz-dropdown">通知</li>
                <li class="jz-dropdown" :class="ishover?'navbar-hover':''">
                    <el-dropdown @visible-change="change">
                        <span class="el-dropdown-link">
                             admin<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item @click.native="postHess">测试</el-dropdown-item>
                            <el-dropdown-item @click.native="setting">测试2</el-dropdown-item>
                            <el-dropdown-item @click.native="testOrgin">测试跨域数据</el-dropdown-item>
                            <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </li>
            </ul>
        </div>
    </header>
</template>

<script>
import { getMenu } from '@/api/user'
import { testHessian } from '@/api/test'
export default {
  data() {
      return {
          ishover: false
      }
  },
  methods: {
      change(val) {
          this.ishover = val
      },
      logout() {
          this.$confirm('确认退出系统？','退出系统', {
              comfirmButtonText: '确定',
              cacelButtonText: '取消'
          }).then(() => {
              this.$store.dispatch('LogOut').then(response => {
                  location.reload()
              })
          })
      },
      setting() {
          this.$store.dispatch('setPositions','layLeft').then(response => {
              location.reload()
          })
      },
      testOrgin() {
          getMenu()
      },
      postHess() {
        debugger
        testHessian.call(null)
      }
  }
}
</script>

<style>
    header {
        padding: 0 20px;
        height: 68px;
        background: url(../../../assets/hbg-1.png);
        background-size: 100%;
    }
    .top-left-logo {
        height: 68px;
        float: left;
    }
    .logo-icon {
        height: 68px;
        width: 68px;
        background: url(../../../assets/image/title-logo.png);
        background-size: 100%;
        display: inline-block;
    }
    h1.logo-text {
        height: 68px;
        line-height: 68px;
        display: inline-block;
        margin: 0;
        color: #fff;
        vertical-align: top;
        font-weight: normal;
        font-family: 'Open Sans', Arial, sans-serif
    }
    .top-right-action {
        float: right;
        height: 68px;
    }
    .top-right-action ul {
        list-style: none;
        margin: 0;
        padding: 0;
        height: 68px;
        line-height: 68px;
        color: #fff;
    }
    .top-right-action ul li {
        padding: 0 12px;
        height: 68px;
        display: inline-block;
        vertical-align: text-bottom;
    }
    li.navbar-hover {
        background-color: #0093dd;
        cursor: pointer;
    }
    .el-dropdown {
        vertical-align: super;
        color: #fff;
    }
    .el-dropdown-menu {
        min-width: 225px;
    }
    .el-dropdown-link {
        display: block;
    }
</style>




