<template>
    <div class="content-index">
        <el-row>
            <el-col :span="24">
                <el-card class="box-card" style="height:300px;">
                    <div slot="header" class="clearfix">
                        <span>待办事项</span>
                        <el-button style="float: right; padding: 3px 0" type="text">
                            <i class="el-icon-refresh"></i>
                        </el-button>
                    </div>
                    <div>
                        <el-table :data="tableData" border style="width:100%">
                            <el-table-column prop="id" label="ID" width="50"></el-table-column>
                            <el-table-column prop="date" label="创建日期" width="180"></el-table-column>
                            <el-table-column prop="name" label="用户名"></el-table-column>
                            <el-table-column prop="address" label="用户住址"></el-table-column>
                        </el-table>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="16" >
                 <el-card class="box-card" style="height:300px;">
                    <div slot="header" class="clearfix">
                        <span>已办事项</span>
                        <el-button style="float: right; padding: 3px 0" type="text">
                            <i class="el-icon-refresh"></i>
                        </el-button>
                    </div>
                    <div v-html="aa">
                    </div>
                </el-card>
            </el-col>
            <el-col :span="8" >
                 <el-card class="box-card" style="height:300px;">
                    <div slot="header" class="clearfix">
                        <span>已办事项</span>
                        <el-button style="float: right; padding: 3px 0" type="text">
                            <i class="el-icon-refresh"></i>
                        </el-button>
                    </div>
                    <div>
                        4545551515
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
  data() {
      return {
          tableData: [
              {
                  id: '1',
                  date: '2018-01-12',
                  name: 'xxxx',
                  address: 'xxxxxxxxxxxx'
              },
              {
                  id: '2',
                  date: '2018-01-13',
                  name: 'xxxx',
                  address: 'xxxxxxxxxxxxxx'
              },
              {
                  id: '3',
                  date: '2018-01-14',
                  name: 'xxxx',
                  address: 'xxxxxxxxxxxx'
              },
              {
                  id: '4',
                  date: '2018-01-15',
                  name: 'xxxxxxxxxxxx',
                  address: 'xxxxxxxxxxxxx'
              }
          ],
          aa: '<span>656555</span>'
      }
  }
}
</script>

<style>
.box-card {
    margin-right: 5px;
}
</style>




