import {aysncRouterMap, constantRouterMap} from '@/router'
import { resolve } from 'path';

/**
 * 通过meat.role 判断是否与当前用户权限匹配
 * @param roles
 * @param route
 */
function hasPermission (roles, route) {

}

/**
 * 通过递归异步路由表，返回符合用户角色权限的路由表
 * @param asyncRouterMap
 * @param roles
 */
function filterAysncRouter (aysncRouterMap, roles) {

}

const permission = {
    state: {
        routers: constantRouterMap,
        addRouters: []
    },
    mutations: {
        SET_ROUTERS: (state, routers) => {
            state.addRouters = routers
            state.routers = constantRouterMap.concat(routers)
        }
    },
    actions: {
        GenerateRouters: ({ commit }, data) => {
            return new Promise(resolve => {
                const { roles } = data
                let accessedRouters
                if (roles.indexof('admin') > 0){
                    accessedRouters = aysncRouterMap
                } else {
                    accessedRouters = filterAysncRouter(aysncRouterMap, roles)
                }
                commit('SET_ROUTERS',accessedRouters)
                resolve()
            })
        }
    }
}

export default permission


