import { resolve } from "path"
import { setPositionSession } from '@/unit/unit'

const showPosition = {
    state: {
        showPosition: 'layLeft'
    },
    mutations: {
        SET_POSITION: (state, showPosition) => {
            state.showPosition = showPosition
        }
    },
    actions: {
        setPositions ({ commit }, showPositions) {
            const posState = showPositions
            return new Promise(resolve => {
                commit('SET_POSITION', posState)
                setPositionSession(posState)
                resolve(posState)
            })
        }
    }
}

export default showPosition



