<template>
    <section :class="showPosition=='layLeft'?'sidebar':''" @mousewheel="weel($event)" :style="style" ref="scrollContanier">
        <!--侧边导航-->
        <el-menu
            v-if="showPosition=='layLeft'?true:false"
            class="el-menu-demo" >
            <el-collapse accordion>
                <el-collapse-item>
                    <template slot="title">
                        <i class="el-icon-setting titleGroup"></i>
                        <span>Consistency</span>
                    </template>
                    <el-menu-item :index="item.menuId" v-for="(item,key) in menuList" :key="key" @click="dump(item)">
                        <i class="el-icon-menu"></i>
                        <span>{{item.menuName}}</span>
                    </el-menu-item>
                </el-collapse-item>
                <el-collapse-item>
                    <template slot="title">
                        <i class="el-icon-setting titleGroup"></i>
                        <span>Efficiency</span>
                    </template>
                    <el-menu-item :index="item.menuId" v-for="(item,key) in menuList" :key="key" @click="dump(item)">
                        <i class="el-icon-menu"></i>
                        <span>{{item.menuName}}</span>
                    </el-menu-item>
                </el-collapse-item>
            </el-collapse>
        </el-menu>
        <!--水平导航-->
        <el-menu
            v-else
            class="el-menu-demo"
            mode="horizontal">
            <el-collapse v-model="activeIndex"  accordion>
                <el-submenu index="1">
                    <template slot="title">
                        <i class="el-icon-setting titleGroup"></i>
                        <span>Consistency</span>
                    </template>
                    <el-menu-item :index="item.menuId" v-for="(item,key) in menuList" :key="key" @click="dump(item)">
                        <i class="el-icon-menu"></i>
                        <span>{{item.menuName}}</span>
                    </el-menu-item>
                </el-submenu>
                <el-submenu index="2">
                    <template slot="title">
                        <i class="el-icon-setting titleGroup"></i>
                        <span>Efficiency</span>
                    </template>
                    <el-menu-item :index="item.menuId" v-for="(item,key) in menuList" :key="key" @click="dump(item)">
                        <i class="el-icon-menu"></i>
                        <span>{{item.menuName}}</span>
                    </el-menu-item>
                </el-submenu>
            </el-collapse>
        </el-menu>
    </section>
</template>

<script>
import { getMenu } from '@/api/user'
import { getPositionSession } from '@/unit/unit'

export default {
  data() {
      return {
          activeName: 1,
          activeIndex: 1,
          collapse: false,
          style: {
              top: '68px'
          },
          style1: {
              top: '0'
          },
          menuList: [{
              'menuName': '系统管理',
              'menuId': '1',
              'routerName': 'systemManage',
              'menuUrl': null
          },{
              'menuName': '菜单管理',
              'menuId': '2',
              'routerName': 'menuList',
              'menuUrl': null
          },
          {
              'menuName': '用户管理',
              'menuId': '3',
              'routerName': 'userManage',
              'menuUrl': null
          },{
              'menuName': 'iframe',
              'menuId': '4',
              'routerName': 'iframe',
              'menuUrl': 'http://192.168.0.18:8080/jzee-web-admin/'
          }]
      }
  },
  computed: {
      showPosition () {
          return getPositionSession().positionState || this.$store.state.showPosition.showPosition

      }
  },
  methods: {
      handleOpen() {
          this.$message({
              message: '打开菜单',
              type: 'success'
          })
      },
      handleClose() {
          this.$message({
              message: '关闭菜单',
              type: 'error'
          })
      },
      weel(e) {
          console.log(e)
          if (e.wheelDelta > 0) {
              if (Number(this.style1.top.replace('px','')) >=0 )  return
              this.style1.top =Number(this.style1.top.replace('px','')) + 10 + 'px'
          } else {
              console.log(this.$refs.scrollContanier.offsetHeight)
              let scrollContanier = this.$refs.scrollContanier.offsetHeight
              let srcollBar = document.getElementById('scrollBar').offsetHeight
              if (srcollBar - Math.abs(Number(this.style1.top.replace('px',''))) <= scrollContanier) return
              this.style1.top =Number(this.style1.top.replace('px','')) - 10 + 'px'
          }
      },
      dump(val) {
          let temp = [{ id: val.routerName, text: val.menuName}]
          this.$store.dispatch('updatevisitedViews', temp)
          .then(res => {
              if (val.menuUrl !== null) {
                  this.$store.dispatch('setIframeUrl', val.menuUrl)
              }
              this.$router.push({
                name: val.routerName
              })
          })
      }
  },
  created: function () {
      /* getMenu().then(res => {
          this.menuList = res.data
      }) */
  }
}
</script>

<style scoped>
.sidebar {
    position: absolute;
    top: 68px;
    bottom: 0;
    overflow: hidden;
    min-height: calc(100% - 68px);
    width: 195px;
    border-right: solid 1px #e6e6e6;
}
.sidebar .el-menu {
    border-right: none;
}
ul.el-menu li {
    border-top: 1px solid #ececec;
}
.el-menu-item-group__title {
    padding: 0;
}
.el-menu-item-group li:first-child {
    border-top: none;
}
.titleGroup{
    margin: auto 10px;
    font-size: 16px;
}
</style>




