/**
 * getter
 */

 const getters = {
     sidebar: state => state.app.sidebar,
     visitedViews: state => state.tagview.visitedViews,
     activeTagView: state => state.tagview.activeTagView,
     cachedViews: state => state.tagview.cachedViews,
     showPosition: state => state.showPosition.showPosition,
     token: state => state.user.token,
     name:  state => state.user.name,
     status: state => state.user.status,
     roles: state => state.user.roles,
     setting: state => state.user.setting,
     permisss_routers: state => state.permission.routers,
     addRouters: state => state.permission.addRouters,
     iframeurl: state => state.app.iframeurl
 }
 export default getters



