import { param2Obj } from '@/unit'

const userMap = {
    admin: {
        role: ['admin'],
        token: 'admin',
        introduction: '超级管理员',
        name: '超级管理员'
    },
    editor: {
        role: ['editor'],
        token: 'editor',
        inheritAttrs: '编辑',
        name: '普通用户'
    }
}

export default {
    loginByUsername: config => {
        const { username } =JSON.parse(config.body)
        return userMap[username]
    },
    getUserInfo: config => {
        const {token} = param2Obj(config.url)
        if (userMap[token]) {
            return userMap[token]
        } else {
            return false
        }
    },
    logout: () => 'success'
}


