<template>
    <div class="login">
        <el-input type="text" placeholder="用户名" v-model="loginForm.username" clearable></el-input>
        <el-input type="password" placeholder="密码" v-model="loginForm.password" clearable @keydown.native.13="login"></el-input>
        <el-button @click="login">登录</el-button>
    </div>
</template>

<script>

export default {
  data() {
      return {
          loginForm: {
              username: '',
              password: ''
          }
      }
  },
  methods: {
      login() {
          if(this.loginForm.username && this.loginForm.password){
              this.$message({
                  message: '登陆成功',
                  type: 'success'
              })
              this.$store.dispatch('LoginByUsername', this.loginForm)
              .then(response => {

                  this.$router.push({
                      path: '/'
                  })
              })
          } else {
              this.$message({
                  message: '用户名和密码不能为空',
                  type: 'error'
              })
          }
      }
  }
}
</script>


<style>
.login {
    margin: 200px auto;
    width: 500px;
    text-align: center;
}
.el-input {
    margin-bottom: 20px;
}
</style>



