<template>
    <el-row class="full-height">
        <el-col :span="4" class="full-height">
            <el-card class="box-card" style="height:100%;">
                <div slot="header" class="clearfix">
                    <span>组织机构</span>
                </div>
                <div>
                    <el-input v-model="searchVal" placeholder="搜索"></el-input>
                    <el-tree
                        v-loading="loading"
                        :data="data2"
                        show-checkbox
                        node-key="id"
                        :default-expanded-keys="[2, 3]"
                        :default-checked-keys="[5]"
                        :props="defaultProps"
                        :filter-node-method="filterNode"
                        ref="tree2">
                    </el-tree>
                </div>
            </el-card>
        </el-col>
        <el-col :span="20" class="full-height">
            <el-card class="box-card" style="height:100%;">
                <div slot="header" class="clearfix">
                    <span>用户列表</span>
                </div>
                <div>
                    <div class="toolbar">
                        <el-button>新增</el-button>
                        <el-button type="primary">修改</el-button>
                        <el-button type="success">删除</el-button>
                        <el-button type="info">导出数据</el-button>
                        <el-button type="warning">上传文件</el-button>
                        <el-button type="danger">关联数据</el-button>
                    </div>
                    <el-table :data="tableData" border style="width:100%" :stripe=true>
                        <el-table-column
                            type="selection"
                            width="34"
                            node-click="fack"
                            v-loading="loading1"
                        >
                        </el-table-column>
                        <el-table-column prop="id" label="ID" width="50"></el-table-column>
                        <el-table-column prop="date" label="创建日期" width="180" sortable></el-table-column>
                        <el-table-column prop="name" label="用户名"></el-table-column>
                        <el-table-column prop="address" label="用户住址"></el-table-column>
                        <el-table-column prop="a" label="测试字段"></el-table-column>
                        <el-table-column prop="b" label="测试字典"></el-table-column>
                        <el-table-column
                            label="操作"
                            width="100"
                        >
                            <template slot-scope="scope">
                                <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
    export default {
        data() {
            return {
                searchVal: '',
                loading: false,
                loading1: false,
                data2: [
                    {
                        id: 1,
                        label: '研发部',
                        children: [{
                            id: 4,
                            label: '城投管理研发部',
                            children: [{
                                id: 9,
                                label: '财政研发部'
                            }, {
                                id: 10,
                                label: '智慧研发部'
                            }]
                        }]
                    }, {
                        id: 2,
                        label: '深圳市坪山工作室',
                        children: [{
                            id: 5,
                            label: '深圳市坪山工作室'
                        }, {
                            id: 6,
                            label: '深圳市光明工作室'
                        }]
                    }, {
                        id: 3,
                        label: '深圳市南山工作室',
                        children: [{
                            id: 7,
                            label: '深圳市后海工作室'
                        }, {
                            id: 8,
                            label: '深圳市前海湾工作室'
                        }]
                    }],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                },
                tableData: [
                    {
                        id: '1',
                        date: '2018-01-13',
                        name: '罗荣盛',
                        address: '江西省赣州市',
                        a: '1',
                        b: '2'
                    },
                    {
                        id: '2',
                        date: '2018-01-14',
                        name: '罗荣盛',
                        address: '江西省赣州市',
                        a: '1',
                        b: '2'
                    },
                    {
                        id: '3',
                        date: '2018-01-15',
                        name: '罗荣盛',
                        address: '江西省赣州市',
                        a: '1',
                        b: '2'
                    },
                    {
                        id: '4',
                        date: '2018-01-16',
                        name: '罗荣盛',
                        address: '江西省赣州市',
                        a: '1',
                        b: '2'
                    }
                ]
            }
        },
        mounted: function () {
            setTimeout(() => {
                this.loading = false
            }, 1000)
        },
        methods: {
            handleClick(row) {
                console.log(row);
            },
            fack() {
                this.loading1 = true
                setTimeout(() => {
                    this.loading1 = false
                }, 1000)
            },
            filterNode(value, data) {
                if (!value) return true;
                return data.label.indexOf(value) !== -1;
            }
        },
        watch: {
            searchVal(val) {
                this.$refs.tree2.filter(val);
            }
        }
    }
</script>


<style>

    .full-height {
        height: 100%;
    }

    .el-row .box-card:first-child {
        margin-right: 5px;
    }

    .box-card {
        margin-bottom: 5px;
    }

    .toolbar {
        margin-bottom: 10px;
    }

    .el-tree {
        margin-top: 10px;
    }

</style>



