<template>
    <div class="contanier menu-horizontal">
        <navbar/>
        <Sidebar/>
        <section :class="showPosition=='layLeft'?'warp':''" class="warpNeed">
            <TagView @changeTab="changeTab"/>
            <AppMain/>
        </section>
    </div>
</template>

<script>
import { getPositionSession } from '@/unit/unit'
import { Navbar, TagView, AppMain, Sidebar} from './components'
export default {
  components: {
      Navbar,
      TagView,
      AppMain,
      Sidebar
  },
  data() {
      return {
          activeTab: "index"
      }
  },
  computed: {
      showPosition () {
          return getPositionSession().positionState || this.$store.state.showPosition.showPosition;
      }
  },
  created() {
      this.$router.push({
          name: this.activeTab
      })
  },
  methods: {
      changeTab(val) {
          this.activeTab = val
      }
  },
  watch: {
      activeTab(newVal,oldVal) {
          this.$router.push({
              name: newVal
          })
      }
  }
}
</script>

<style>
.contanier {
    height: 100%;
}
.warpNeed{
    height: calc(100% - 68px);
}
.menu-horizontal .warp {
    padding-left: 195px;
}
</style>




