import Cookies from 'js-cookie'
const TokenKey = 'Admin-Token'
const Session = 'JSESSIONID'
const positionState= ''

export function getToken () {
    return Cookies.get(TokenKey)
}

export function setToken (token) {
    return Cookies.set(TokenKey, token)
}

export function removeToken () {
    return Cookies.remove(TokenKey)
}
export function removeSEssion () {
     Cookies.remove('SESSION')
     Cookies.remove('JSESSIONID')
}

export function setSession () {
     Cookies.set('SESSION','339febdc-287c-4c59-b60c-db93e933ea2d')
}

export function setPositionSession (positionVal) {
    Cookies.set('positionState',positionVal)
}

export function getPositionSession () {
    return Cookies.get(positionState)
}



