import request from '@/unit/request'

export function getMenu () {
    return request({
        url: '/admin/sysMenu/getLeftMenuJsonCeshi.do',
        method: 'get'
    })
}
export function getHessianData () {
  return request({
    url: '/hessian/hessianService/sayHello',
    method: 'post'
  })
}



