
export function testHessian () {
    var Proxy = require('hessian-proxy').Proxy
    var proxy = new Proxy('http://192.168.0.18:8080/jzee-web-admin/hessian/hessianService/ ','','',proxy)
    proxy.invoke('sayHello',null,function(err,reply){
        console.log('test0: ' + JSON.stringify(reply));
    })
}


