import { resolve } from "path";
import { debug } from 'util';

/**
 * 添加tab页
 * @param visitedViews   状态管理里面的存的，也就是当前打开的tab页
 * @param tagView 即将要打开的tab
 * return 新的tab数组
 */
function addTagView(visitedViews, tagView) {
    let hasTag = visitedViews.some(res => {
                    return res.id === tagView[0].id
                 })
    if (hasTag) return visitedViews
    return visitedViews.concat(tagView)
}
/**
 * 删除数组的tab
 * @param visitedViews   状态管理里面的存的，也就是当前打开的tab页
 * @param tagView 即将要打开的tab
 * retun 新的tab数组
 */
function delTagView(visitedViews, tagView) {
    let newVisitedViews = visitedViews.filter(res => {
        if(tagView.id !== res.id){
            return res
        }
    })
    return newVisitedViews
}
/**
 * 删除除当前的tab
 * @param visitedViews   状态管理里面的存的，也就是当前打开的tab页
 * @param tagView 即将要打开的tab
 */
function filterOthers(visitedViews, tagView) {
    let newVisitedViews = visitedViews.filter(res => {
        if(tagView.id === res.id || res.id === 'default'){
            return res
        }
    })
    return newVisitedViews
}

const tagview = {
    state: {
        visitedViews: [
            {
                id: 'default',
                text: '首页',

            }
        ],
        activeTagView: 'default'
    },
    mutations: {
        ADD_OPENTAGVIEW: (state, tagviews) => {
            state.visitedViews = tagviews
        },
        DEL_OPENTAGVIEW: (state, tagview) => {
            state.visitedViews = tagview
        },
        SET_ACTIVETAGVIEW: (state, currentTagView) => {
            state.activeTagView = currentTagView
        }
    },
    actions: {
        updatevisitedViews ({ commit, state }, currentTagView) {
            const activeTagView  = currentTagView
            return new Promise(resolve => {
                let tagviews = addTagView (state.visitedViews, activeTagView)
                commit('ADD_OPENTAGVIEW', tagviews)
                commit('SET_ACTIVETAGVIEW', activeTagView[0].id)
                resolve(state.visitedViews)
            })
        },
        changeActiveTag({ commit }, activeId) {
            const id = activeId
            return new Promise(resolve => {
                commit('SET_ACTIVETAGVIEW', id)
                resolve()
            })
        },
        delAll({ commit }) {
            return new Promise(resolve => {
                commit('DEL_OPENTAGVIEW', [{id: 'default',text: '首页'}])
                commit('SET_ACTIVETAGVIEW', 'default')
                resolve()
            })
        },
        delCurrent({ commit, state }, current) {
            const currentTag = current
            return new Promise(resolve => {
                let newVisitedViews = delTagView(state.visitedViews, currentTag)
                commit('DEL_OPENTAGVIEW', newVisitedViews)
                commit('SET_ACTIVETAGVIEW', newVisitedViews[newVisitedViews.length-1].id)
                resolve()
            })
        },
        delOthers({ commit, state }, current) {
            const currentTag = current
            return new Promise(resolve => {
                let newVisitedViews = filterOthers(state.visitedViews, currentTag)
                commit('DEL_OPENTAGVIEW', newVisitedViews)
                resolve()
            })
        }
    }
}

export default tagview



